.modal {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
  }
  .ytvid{
    padding-top: 5%;
    justify-content: center;
}
  .modal-content {
    background: #fff;
    padding: 20px;
    border-radius: 8px;
    max-width: 800px;
    width: 100%;
    position: relative;
  }
  .Report {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%
    ; /* Make the PDF container take up the full width of the modal */
  }
  .rpv-core__page {
    margin-bottom: 20px; /* Adjust the value as needed */
}
.Video{
    border-radius: 100%;
}
.github-link {
  color: #0366d6; /* GitHub blue */
  text-decoration: none;
  font-weight: bold;
}

.github-link:hover {
  text-decoration: underline;
}
